<template>
  <div>
    <audio controls="" autoplay="" name="media"><source src="http://dl.stream.qqmusic.qq.com/C400003OIP8A3BE5uK.m4a?vkey=A354F9A6B118F47BD7AA2487867625D978DBF4B019B9424045E30E355EECA364051BA2663CFEC1F76E42B2728B42D73B6366BF685F51A087&amp;guid=6226037951&amp;uin=0&amp;fromtag=66" type="audio/mp4"></audio>
    <canvas id="canvas" class="background" :width="width" :height="height"></canvas>
    <canvas id="postcard" width="600" height="400"></canvas>
  </div>
</template>

<script>
class Point {
  constructor (x, y, context) {
    this.context = context
    this.x = x
    this.y = y
    this.size = 1
    this.opacity = 1
  }

  draw () {
    this.context.save()
    this.context.fillStyle = 'rgba(164, 81, 11, 255)'
    this.context.beginPath()
    this.context.arc(this.x, this.y, this.size, 0, 2 * Math.PI, true)
    this.context.fill()
    this.context.restore()
  }
}

export default {
  data: function () {
    return {
      context: "HAPPY Valentine's DAY",
      can: null,
      cxt: null,
      canPost: null,
      cxtPost: null,
      points: [],
      width: window.innerWidth,
      height: window.innerHeight,
      particles: [],
      particleNum: 200
    }
  },
  created () {
  },
  mounted () {
    this.initBackground()
    this.drawBackground()
    setInterval(this.move, 10)
    this.initPostcard()
    this.drawPostcard()
  },
  methods: {
    initBackground: function () {
      this.can = document.getElementById('canvas')
      this.cxt = this.can.getContext('2d')
      for (let i = 0; i < this.particleNum; i++) {
        this.particles.push({
          position: {x: this.getRandom(this.width), y: this.getRandom(this.height)},
          moveDirec: {x: Math.random() > 0.5 ? Math.random() * 1 : -Math.random() * 1, y: Math.random() > 0.5 ? Math.random() * 1 : -Math.random() * 1},
          size: this.getRandom(1) * 0.5,
          opacity: this.getRandom(1)
        })
      }
    },
    drawBackground: function () {
      this.cxt.clearRect(0, 0, this.can.width, this.can.height)
      this.cxt.fillStyle = '#444'
      this.particles.forEach(p => {
        this.cxt.fillStyle = 'rgba(255, 255, 255, ' + p.opacity + ')'
        this.cxt.beginPath()
        this.cxt.arc(p.position.x, p.position.y, p.size * 10, 0, 2 * Math.PI, true)
        this.cxt.fill()
      })
    },
    initPostcard: function () {
      this.canPost = document.getElementById('postcard')
      this.cxtPost = this.canPost.getContext('2d')
      this.cxtPost.font = '40px Adele bold'
      this.cxtPost.fillText(this.context, this.canPost.width / 6, this.canPost.height / 4)
      this.cxtPost.save()
      let imgData = this.cxtPost.getImageData(0, 0, this.canPost.width, this.canPost.height)
      console.log('imgData', imgData.data)

      for (let x = 0; x < imgData.width; x += 3) {
        for (let y = 0; y < imgData.height; y += 3) {
          let i = (y * imgData.width + x) * 4
          if (imgData.data[i + 3] >= 1) {
            let point = new Point(x - 3, y - 3, this.cxtPost)
            this.points.push(point)
          }
        }
      }
    },
    drawPostcard: function () {
      this.cxtPost.clearRect(0, 0, this.canPost.width, this.canPost.height)
      this.points.forEach(p => {
        p.draw()
      })
    },
    move: function () {
      this.particles.forEach(p => {
        p.position.x += p.moveDirec.x
        p.position.y += p.moveDirec.y
        if (p.position.x > this.width || p.position.x <= 0) {
          p.moveDirec.x = -p.moveDirec.x
        }
        if (p.position.y > this.height || p.position.y <= 0) {
          p.moveDirec.y = -p.moveDirec.y
        }
      })
      this.drawBackground()
    },
    getRandom: function (num) {
      return Math.random() * num
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
audio {
  position: absolute;
  z-index: 1000;
}
.background {
  position: absolute;
  background-image: url('../../assets/background.jpg');
}

#postcard {
  background-image: url('../../assets/postcard.jpg');
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
}
</style>



