<template>
  <div class="lab-container">
    <div class="single-lab" v-show="labShow" v-for="lab in labs" v-on:click="showLab(lab.link)">
      <p class="lab-name">{{lab.name}}</p>
      <p class="lab--introduce">{{lab.introduce}}</p>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      labShow: true,
      labs: [
        {
          name: 'ReaderAssist',
          link: 'ReaderAssist',
          introduce: 'a tool for reader add marks and words in web'
        },
        {
          name: 'RangeClear',
          link: 'RangeClear',
          introduce: 'a game showed in best brain'
        },
        {
          name: '情人节',
          link: 'Valentine',
          introduce: "a Valentine's gift for my JX"
        }
      ]
    }
  },
  created () {
  },
  update () {
  },
  methods: {
    showLab: function (e) {
      this.labShow = false
      this.$router.push({name: e})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lab-container {
  clear: both;
  height: auto;
  padding-bottom: 30px;
}

.lab-container:after {
  content: '';
  display: block;
  clear: both;
  visibility: hidden;
}
.single-lab {
  width: 300px;
  height: 300px;
  float: left;
  margin-left: 20px;
  margin-top: 30px;
  top: 20px;
  border: 1px solid;
  text-align: center;
  cursor: pointer;
}

.single-lab:hover {
}

.lab--introduce {
  bottom: 5px;
}
</style>



